interface Props {
    time: number;
}
const getMinutes = (time:number) =>  {
    return (
    (time % 60) < 10 
        ? "0"+(time % 60).toFixed() 
        : (time % 60).toFixed()
    )
}
const Timer = (props: Props) => <p><strong>Time: </strong>{Math.floor(props.time / 60) || "00"}:{getMinutes(props.time)}</p>
export default Timer